/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    top: 14px;
    right: 20px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Color/shape of burger icon bars on hover*/
  /* .bm-burger-bars-hover {
    background: #a90000;
  } */
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
    top: 56px;
    width: 100% !important;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #f0f0f0;
    padding:  0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }

  .bm-item-list nav {
    background:#f0f0f0;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
    display: none;
  }
  .brand{    position: absolute;
    left: 15px;
    top: 50%;
    transform: translate(0,-50%);
    font-size: 18px;
    font-family: 'Calistoga', cursive;
    font-weight: "400";
    color: #00baa9;
  }

  .makeStyles-sectionMobile-26 {
    display: block;
    width: 100%;
}

.homelink{
  color: inherit;
  text-decoration: none;
}